body, h1, h2, h3, h4, h5, h6, p, a, li, dl, dd, dt, a, div, p, span, button, .clear-fixed {
  margin: 0;
  padding: 0;
  border-radius: 0;
  list-style: none;
  box-sizing: border-box;
}
body, h1, h2, h3, h4, h5, h6, p, a{
}
li, dl, dd, dt {
  list-style: none;
}
a{
  text-decoration: none;
}
div, p, span {
  box-sizing: border-box;
}
button {
  border: none;
  background-color: transparent;
}
.clear-fixed {
}
.clear-fixed:after {
  content: '';
  display: table;
  clear: both;
  height: 0px;
  font-size: 0px;
}



